'use strict';

import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import errorSvg from '../img/symbol-defs.svg';
const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    userSelectedDate = selectedDates[0];
    console.log(selectedDates[0]);
    selectedDate();
  },
};
flatpickr('#datetime-picker', options);

const datetimePicker = document.querySelector('#datetime-picker');
const startButton = document.querySelector('button[type="button"]');

const timerValues = {
  days: document.querySelector('[data-days]'),
  hours: document.querySelector('[data-hours]'),
  minutes: document.querySelector('[data-minutes]'),
  seconds: document.querySelector('[data-seconds]'),
};

let userSelectedDate = null;
let countdownInterval = null;

startButton.disabled = true;

function selectedDate() {
  const now = new Date();
  if (userSelectedDate <= now) {
    iziToast.error({
      title: 'Error',
      titleColor: '#fff',
      titleSize: '16px',
      titleLineHeight: 1.5,
      position: 'topRight',
      message: 'Please choose a date in the future',
      messageSize: '16px',
      messageLineHeight: 1.5,
      messageColor: '#fff',
      backgroundColor: '#ef4040',

      closeOnEscape: true,
      icon: 'error',
      iconUrl: errorSvg,
      timeout: 3000,
      theme: 'dark',
    });
    startButton.disabled = true;
  } else {
    startButton.disabled = false;
  }
}
startButton.addEventListener('click', handleClick);

function handleClick() {
  startButton.disabled = true;
  datetimePicker.disabled = true;

  if (countdownInterval) return;

  countdownInterval = setInterval(() => {
    const now = new Date();
    const timeRemaining = userSelectedDate - now;

    if (timeRemaining <= 0) {
      clearInterval(countdownInterval);
      datetimePicker.disabled = false;
      resetTimer();
      startButton.disabled = false;
    } else {
      const time = convertMs(timeRemaining);
      updatedTimer(time);
    }
  }, 1000);
}

function convertMs(ms) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  const days = Math.floor(ms / day);
  const hours = Math.floor((ms % day) / hour);
  const minutes = Math.floor(((ms % day) % hour) / minute);
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);

  return { days, hours, minutes, seconds };
}
function updatedTimer({ days, hours, minutes, seconds }) {
  timerValues.days.textContent = addLeadingZero(days);
  timerValues.hours.textContent = addLeadingZero(hours);
  timerValues.minutes.textContent = addLeadingZero(minutes);
  timerValues.seconds.textContent = addLeadingZero(seconds);
}
function resetTimer() {
  updatedTimer({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });
}
function addLeadingZero(value) {
  return String(value).padStart(2, '0');
}
